/* for all screens */
.row-padding {
  padding: 50px 5vw;
  margin: 0px;
}

.about,
.contact,
.portfolio,
.resume {
  background-color: #F0F0F0;
}

/* for small screens */
.container-title {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: black;
  padding: 5px;
  margin-top: 0;
  margin-bottom: 10px;
}

.about-img {
  height: 150px;
  width: 150px;
  border: solid 7px #757575;
}

.about-description {
  font: #000000;
  margin: 0;
}

.submit-btn {
  margin: 0;
}

.proficiencies {
  padding-bottom: 10px; 
}

.resume-heading {
  font-weight: 600;
}

.resume-link {
  margin: 5px;
}

.resume-link:hover {
  background-color: #4d3a97;
}

/* for medium screens */
@media only screen and (min-width: 600px) {
  .container-title {
    font-size: 2rem;
    margin-top: 5px; 
    margin-bottom: 15px;
  }
  
  .about-img {
    height: 200px;
    width: 200px;
  }
  
  .about-description {
    font-size: 1.5rem;
  }
}

/* for large screens */
@media only screen and (min-width: 992px) {
  .container-title {
    font-size: 2.25rem;
    margin-top: 5px; 
    margin-bottom: 15px;
  }
  
  .about-img {
    height: 250px;
    width: 250px;
  }
  
  .about-description {
    font-size: 1.15rem
  }
}